	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Manila: Converted","date":"24 Jun 2004","slug":"/2004-06-24-manila-converted/","tags":"Y2004"},"html":"<p><a href=\"http://www.papascott.de/ConvertingManila/\">Good News!</a> <a href=\"http://q.queso.com/\">Jason Levine's</a> Frontier script has worked! <a href=\"http://www.2020hindsight.org/\">Susan</a> now has a working MT Import File. Jason says he'll be posting his script in the next few days, and since UserLand offfers a 60-day trial version of Manila for download (thanks, <a href=\"http://www.ahawkins.org/blog/\">Alwin</a>, for the <a href=\"http://www.intertwingly.net/blog/2004/06/22/Converting-Manilla#c1087948312\">tip</a>), I'm hopeful that we can come up with a procedure for people to export their own sites. Many thanks to <a href=\"http://www.intertwingly.net/blog/\">Sam Ruby</a> for the A-list publicity and support. </p>\n<p>&lt;snideComment&gt;Funny that a bunch of ex-Manila users had to do this by themselves, and that noone from UserLand or weblogs.com even acknowledged our effort.&lt;/snideComment&gt;</p>"},"next":{"fields":{"title":"Converting Manila","date":"22 Jun 2004","slug":"/2004-06-22-converting-manila/"}},"previous":{"fields":{"title":"For Housewives","date":"25 Jun 2004","slug":"/2004-06-25-for-housewives/"}}},{"node":{"fields":{"title":"Converting Manila","date":"22 Jun 2004","slug":"/2004-06-22-converting-manila/","tags":"Y2004"},"html":"<p>Back in April 2002, I converted my Manila weblog at editthispage.com to Movable Type using XML files exported from Manila and a simple Perl script (using XML::Twig).</p>\n<p>This month (2 weeks <em>before</em> the weblogs.com outage), Susan Kitchens found my old script and asked me about converting her Manila site <a href=\"http://www.2020hindsight.org\">http://www.2020hindsight.org</a> (after she had painstakingly extracted her site into XML). I thought that the <a href=\"http://www.movabletype.org/docs/mtimport.html\">Movable Type Import Format</a> would be a good target, since it can be imported into many different weblogging systems.</p>\n<p>However, Manila had become more complicated in the meantime, and I was unable to quickly apply my script to Susan's files (i.e. I've gotten stuck), and I'm short of time. Since a <a href=\"http://www.intertwingly.net/blog/2004/06/15/Archive-Restore\">number of people</a> had publicly offered to help export Manila sites, people with a probably a better understanding of Manila and XML than I have, I decided to plea for help via a Wiki page entitled <a href=\"http://www.papascott.de/ConvertingManila\">ConvertingManila</a>.  Any help would be greatly appreciated.</p>"},"next":{"fields":{"title":"New Religion","date":"21 Jun 2004","slug":"/2004-06-21-new-religion/"}},"previous":{"fields":{"title":"Manila: Converted","date":"24 Jun 2004","slug":"/2004-06-24-manila-converted/"}}},{"node":{"fields":{"title":"New Religion","date":"21 Jun 2004","slug":"/2004-06-21-new-religion/","tags":"Y2004"},"html":"<p>I <a href=\"http://www.papascott.de/archives/2004/06/13/zidane-is-god/\">take</a> it back. <a href=\"http://news.bbc.co.uk/sport1/hi/football/euro_2004/3787401.stm\" title=\"BBC SPORT | Football | Euro 2004 | Live: Croatia 2-4 England\">Rooney</a> (45+1, 68) is God.</p>"},"next":{"fields":{"title":"Auf die Palme","date":"21 Jun 2004","slug":"/2004-06-21-auf-die-palme/"}},"previous":{"fields":{"title":"Converting Manila","date":"22 Jun 2004","slug":"/2004-06-22-converting-manila/"}}}],"pathPrefix":"","first":false,"last":false,"index":462,"pageCount":1101}}

